import TextField from '@material-ui/core/TextField';
import { useCallback, useRef, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import CheckButton from 'react-validation/build/button';
import Form from 'react-validation/build/form';
import processmakerLogoFinal from '../../assets/images/branding/processmakerLogoFinal.png';
import { resetPasswordIcon } from '../../assets/images/icons';
import { forgetPasswordRequest } from '../../services/auth.service';
import { Button } from '../common';
import './ResetPassword.scss';

const ResetPassword = () => {
	const navigate = useNavigate();
	const form = useRef();
	const checkBtn = useRef();
	const [email, setEmail] = useState('');
	const [loading, setLoading] = useState(false);
	const [, setShowHelp] = useState(false);
	const [message, setMessage] = useState('');
	const [sent, setSent] = useState(false);

	const handleRequest = useCallback(
		e => {
			e.preventDefault();
			setLoading(true);
			form.current.validateAll();
			if (checkBtn.current.context._errors.length === 0) {
				forgetPasswordRequest(email).subscribe({
					next: _data => {
						setSent(true);
						setMessage('');
					},
					error: error => {
						console.log(error);
						setSent(false);
						setMessage(error.data);
						setLoading(false);
					},
					complete: () => {
						setLoading(false);
					},
				});
			} else {
				setLoading(false);
			}
		},
		[email]
	);

	return (
		<div id="reset-password-container" className="main-container">
			<div className={'first-container'}>
				<div
					style={{
						display: 'flex',
						width: '100%',
						flexDirection: 'column',
					}}
				>
					<div
						style={{
							alignSelf: 'flex-start',
							display: 'flex',
							flexDirection: 'column',
							justifyContent: 'space-evenly',
							alignItems: 'center',
						}}
					>
						<div style={{ alignSelf: 'flex-start', width: '350px' }}>
							<h2
								style={{
									color: '#4A4A4A',
									fontFamily: 'var(--app-font-poppins)',
									fontSize: '32px',
									fontWeight: 500,
									letterSpacing: '0',
									marginBottom: '0',
								}}
							>
								{sent ? 'Password Reset Sent!' : 'Recover your Password'}
							</h2>
							<p
								style={{
									color: '#4A4A4A',
									fontFamily: 'var(--app-font-quicksand-medium)',
									fontSize: '16px',
									fontWeight: 500,
								}}
							>
								{sent ? 'Follow the instructions in the email.' : 'Drop your company email in the field below and follow the instructions in the email sent.'}
							</p>
						</div>
						<div style={{ paddingTop: '30px' }}>
							<Form onSubmit={handleRequest} ref={form}>
								{!sent && <TextField id="my-input" type="text" variant="outlined" name="name" className="input-style" placeholder={'Company Email'} value={email} onChange={e => setEmail(e.target.value)} onFocus={() => setMessage('')} />}
								{message?.email ? (
									<div className="form-group">
										<div className={'message-container'} role="alert">
											{message?.email}
										</div>
									</div>
								) : null}
								<div
									style={{
										marginTop: sent ? '0px' : '50px',
										width: '400px',
									}}
								>
									<Button disabled={loading} loading={loading} clicked={handleRequest} title={'Resend'} style={{ margin: '0', width: '100%' }} />
								</div>
								{message && !message.email && (
									<div className="message-container" role="alert">
										{message}
									</div>
								)}
								<CheckButton style={{ display: 'none' }} ref={checkBtn} />
							</Form>
						</div>
						<div
							style={{
								display: 'flex',
								width: '100%',
								flexDirection: 'row',
								justifyContent: 'flex-start',
							}}
						>
							<p
								onClick={() => navigate('/')}
								style={{
									cursor: 'pointer',
									position: 'absolute',
									bottom: '40px',
									fontFamily: 'var(--app-font-quicksand-medium)',
									color: '#4A4A4A',
									textDecoration: 'underline',
								}}
							>
								Back to Login
							</p>
						</div>
					</div>
				</div>
			</div>
			<div
				className={'second-container'}
				onClick={e => {
					e.preventDefault();
					e.stopPropagation();
					setShowHelp(false);
				}}
			>
				<div
					onClick={e => {
						e.preventDefault();
						e.stopPropagation();
						setShowHelp(true);
					}}
					style={{
						cursor: 'pointer',
						alignSelf: 'flex-end',
						marginRight: '91px',
						display: 'flex',
						position: 'fixed',
						bottom: '0',
						flexDirection: 'row',
						justifyContent: 'flex-end',
						alignItems: 'stretch',
						borderRadius: '8px 8px 0 0',
						backgroundColor: ' #FFFFFF',
						boxShadow: '0 0 40px 0 rgba(0, 0, 0, 0.1)',
					}}
				/>
			</div>
		</div>
	);
};

export default ResetPassword;
